.h50 {
    height: 75vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.h50:nth-child(odd) {
    background: rgba(0, 0, 0, 0.2);
}

.h50:nth-child(even) {
    background: rgba(0, 0, 0, 0.1);
}

img {
    height: 50px;
    width: 50px;
}

#root {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 50%;
    height: 50%;
    border: 3px solid red;
    overflow: auto;
}